@import url('https://fonts.googleapis.com/css?family=Fredoka+One|Mali');



.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



.app{
  display: -ms-flexbox;
  display: flex;
 
 
 }
 .main{
  -ms-flex-preferred-size: 20%;
      flex-basis: 20%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  font-family: 'Fredoka One', cursive;
  color: white;
  font-size: 20px;
  background-color: #1ae1ef;
  padding: 10px;
 }

 select {
  color: #1ae1ef;
  margin-bottom: 40px;
  font-size: 20px;
 }
 .map{
  -ms-flex-positive: 1;
      flex-grow: 1;
  height: 100vh;
  top: 0;
  position: -webkit-sticky;
  position: sticky;
 }
 .place {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: start;
      align-content: flex-start;
 }


